* {
    font-family: "consolas", sans-serif;
    margin: 0;
    padding: 0;

    background: #26262c;
    color: #f6f6f4;
}

h2 {
    padding-top: 6vh;
    padding-bottom: 3vh;
    font-weight: 530;
}

h3 {
    font-weight: 400;
    font-size: 23px;
    padding-bottom: 2%;
}

p {
    padding-top: 0.5vh;
    padding-bottom: 0.5vh;
}

a {
    text-decoration: none;
    font-style: oblique;
}

.a_github {
    color: #8aafda;
    font-style: normal;
}

.a_github:hover {
    color: #FFD799;
}

.main_container {
    padding: 3vh 3vw;

    /* ToDo Временный блок, как сделать чтобы съезжало, как надо */
    padding-bottom: 10vh;

    min-height: 100%;
    display: grid;
    gap: 4vw;
    grid-template-columns: 1fr 3fr;
}

.sidebar {
    text-align: center;
}

.sidebar img {
    width: 100%;
    border-radius: 10px;
}

.sidebar_info {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.content {
    text-align: justify;
}

.sidebar_info_data {
    padding-top: 0.3vw;
    padding-bottom: 0.8vw;
    color: rgba(246, 246, 244, 0.66);

    text-align: right;
}

.sidebar_info_data:hover {
    color: #f6f6f4;
    cursor: pointer;
}

.hr_profile {
    width: 120%;
}

@media screen and (max-width: 600px) {
    .footer {
        grid-area: footer;

        padding: 2%;
        display: flex;
        flex-direction: column;
        background-color: rgb(235, 230, 206);
    }

    .sidebar {
        grid-area: sidebar;
    }

    .content {
        grid-area: content;
    }

    .main_container {
        grid-template-areas: "sidebar sidebar"
        "content content";
    }

    .sidebar_info {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-wrap: nowrap;
    }

    .h2_db {
        padding-left: 5%;
        text-align: center;
    }

    ol {
        padding-left: 10%;
        padding-top: 10px;
    }

    ul {
        text-align: center;

        list-style-type: none;
    }

    p, h3, h4 {
        padding-left: 10%;
        padding-right: 5%;
        padding-top: 10px;
    }

    .icons {
        width: 10vw;
    }

    .subFooter {
        justify-items: center
    }
}